.price__value {
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  padding: 0;
  width: 118px;
  min-height: 38px;
  background-color: @white;
  color: @black;
  box-sizing: border-box;
  border: 2px solid @primary-lighter;
  border-radius: 4px;
  appearance: textfield;
  text-align: center;
  padding: 0 10px;
  &::placeholder {
    color:@grey;
  }

  &:focus {
    border: 2px solid @primary-darker;
    outline: none;
  }
  &:disabled {
    border: 2px solid @bg-light;
  }
}
.price__label-wrapper{
  display: flex;
  justify-content: space-between;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    width: 9px;
    height: 2px;
    background-color: @grey;
    top: 70%;
    left: 50%;
    transform: translateY(50%);
    transform: translateX(-50%);

  }
}

.price__label {
  margin-top: 35px;
}
