.main-header {
  background-color: @white;
  color: @black;
  min-width: @mobile-width;
}

.main-header__logo-holder {
  width: 120px;
  display: block;
  padding: 10px 0 10px 0;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  &:disabled {
    opacity: 0.3;
  }
}

// .main-header__logo {
//   padding: 10px 0 10px 0;
// }

.main-header__logo-text {
  display: none;
}

.main-header__wrapper {
  display: flex;
  padding-left: 20px;
  // flex-direction: column;
  position: relative;
  align-items: center;
}

@media (min-width: @tablet-width) {
  .main-header {
    padding: 0 20px 0 20px;
  }
  .main-header__logo-holder {
    padding: 0;
    width: 82px;
    height: 36px;
    padding-left: 0;
    img {
      height: 100%;
      width: 100%;
    }
  }

  // .main-header__logo {
  //   width: 82px;
  //   height: 36px;
  //   padding-left: 0;
  //   img {
  //     height: 100%;
  //     width: 100%;
  //   }
  // }

  .main-header__wrapper {
    justify-content: space-between;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 0;
  }

  .navigation__toggle {
    display: none;
  }
}


@media (min-width: @desktop-width) {
  .main-header {
    padding: 0 40px 0 40px;
  }

  .main-header__logo {
    text-decoration: none;
  }

  .main-header__logo-holder {
    width: 170px;
    height: 36px;
    margin-right: 20px;
    img {
      height: 100%;
      width: 100%;
    }
  }
  .main-header__logo-wrapper {
    display: flex;
    align-items: center;
  }

  .main-header__logo-text {
    display: block;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #333333;
    width: 126px;

  }

  .main-header__wrapper {
    justify-content: space-between;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}
